import { useEffect } from "react";
import { useState } from "react";
import styled from "styled-components";

import { movieApi } from "../../api";
import { Loader } from "../Loader";
import { MainBanner } from "./MainBanner";
import { Container } from "../Container";
import "../../style/swiper.css";
import { Movies } from "./Movies";
import { PageTitle } from "../PageTitle";
import { PageNotFound } from "../PageNotFound";
// console.log(movieApi.nowPlaying());

const Wrap = styled.div``;

const Section = styled.section``;

export const Home = () => {
  //Usestateë¥¼ ì´ìš©í•˜ì—¬ results ì €ì¥í•˜ê³ , useEffect ë°–ìœ¼ë¡œ ë³€ìˆ˜ í˜¸ì¶œí•  ìˆ˜ ìˆë„ë¡ ë§Œë“¤ê¸°
  //   nowPlaying, upcommingê¹Œì§€

  const [nowPlay, setNowPlay] = useState();
  const [upComing, setUpComing] = useState();
  const [popular, setPopular] = useState();
  const [topRate, setTopRate] = useState();
  const [loading, setLoading] = useState(true);
  const movieNum = 2;

  useEffect(() => {
    const movieData = async () => {
      try {
        //   console.log(await movieApi.nowPlaying());
        const {
          data: { results: nowPlaying },
        } = await movieApi.nowPlaying();
        setNowPlay(nowPlaying);

        const {
          data: { results: upComing },
        } = await movieApi.upComing();
        setUpComing(upComing);

        const {
          data: { results: popular },
        } = await movieApi.popular();
        setPopular(popular);

        const {
          data: { results: topRate },
        } = await movieApi.topRate();
        setTopRate(topRate);

        setLoading(false);
      } catch (error) {
        <PageNotFound />;
      }
    };
    movieData();
  }, []);

  // console.log("í˜„ì¬ ìƒì˜ ì˜í™”", nowPlay);
  // console.log("ê°œë´‰ ì˜ˆì • ì˜í™”", upComing);

  // console.log(nowPlay && nowPlay[0]);
  // => ì²˜ìŒì—” í•„ìˆ˜ë¡œ &&(ìˆë‹¤ë©´) ë„£ì–´ë‹¬ë¼ê³  ì ê¸° ì•„ë‹ˆë©´ ì˜¤ë¥˜ëœ¸

  return (
    <div>
      <PageTitle title="í™ˆ" />

      {loading ? (
        <Loader />
      ) : (
        <>
          {nowPlay && (
            <Wrap>
              <MainBanner now={nowPlay} num={movieNum} />

              <Section>
                <Container>
                  <Movies movieData={nowPlay} title="í˜„ì¬ ìƒì˜ ì˜í™”" />
                  <Movies movieData={upComing} title="ê°œë´‰ ì˜ˆì • ì˜í™”" />
                  <Movies movieData={popular} title="ì¸ê¸° ì˜í™”" />
                  <Movies movieData={topRate} title="ìƒì˜ ìˆœìœ„" />
                </Container>
              </Section>
            </Wrap>
          )}
        </>
      )}
    </div>
  );
};
